import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ExpenseSetupComponent } from './expense-setup.component';
import { ExpenseTrackerSetupGeneralComponent } from './expense-tracker-setup-general/expense-tracker-setup-general.component';

const routes: Routes = [
  { path     : '',component: ExpenseSetupComponent,
		children: [
      { path: '', redirectTo: 'general' },
      { path: 'general', component: ExpenseTrackerSetupGeneralComponent}
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ExpenseSetupRoutingModule { }
