import { Component, OnInit } from '@angular/core';
import { AccountsService } from 'src/app/api/controllers/Accounts';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { SharedService } from 'src/app/shared/services/shared.service';
import { SessionService } from 'src/app/core/session/session.service';

@Component({
  selector: 'app-expense-tracker-setup-general',
  templateUrl: './expense-tracker-setup-general.component.html',
  styleUrls: ['./expense-tracker-setup-general.component.scss']
})
export class ExpenseTrackerSetupGeneralComponent implements OnInit {
  	// tax
	taxData:any = {};
	isEditTax:boolean = true;

  constructor(private accountsService: AccountsService,
		private lookupService: LookupService,
		private sharedService: SharedService,
		private sessionService: SessionService) {
  }
  
  getCustomerTaxList() {
		//Tax
		let taxListParams = {
			ApartmentId: this.sessionService.apartmentId,
			LookupTypeId: 183
		}
		this.lookupService.getLookupValueByLookupTypeId(taxListParams).subscribe((res: any) => {
			if(res.length > 0)
			this.taxData= res[0];
		});
	}

  saveTax() {
		if(this.taxData.lookupValueId) {
			//Edit
			let params = {
				lookupvalue: {                      
					"lookupValueId": this.taxData.lookupValueId,
					"apartmentId": this.sessionService.apartmentId,
					"lookupTypeId": 183,
					"lookupValueName": this.taxData.lookupValueName,
					"description": this.taxData.lookupValueName,
					"isActive": true,
					"updatedBy": parseInt(this.sessionService.userId),
					"updatedOn": new Date().toISOString(),
				}
			}
			this.lookupService.updateLookupValue(params).subscribe((res:any) => {
				if(res) {
					this.getCustomerTaxList();
					this.isEditTax = true;
				}
			})
		} else {
			//create
			let params = {
				lookupvalue: {
					"apartmentId": this.sessionService.apartmentId,
					"lookupTypeId": 183,
					"lookupValueName": this.taxData.lookupValueName,
					"description": this.taxData.lookupValueName,
					"isActive": true,
					"insertedBy": parseInt(this.sessionService.userId),
					"insertedOn": new Date().toISOString(),
					"updatedBy": null,
					"updatedOn": null,
				}
			}
			this.lookupService.addLookupValue(params).subscribe((res:any) => {
				if(res) {
					this.taxData.lookupValueId = res.message;
					this.isEditTax= true;
				}
			})
		}
	}

  ngOnInit(): void {
		this.getCustomerTaxList();
  }

}
